import React from 'react';

function Navigation(props) {
  return (
    <nav className="topNavigation">
      <h1>{props.title}</h1>
      <ul className="nav-btns">
        <li className="btn"><a href={props.plusLink}>{props.plusLabel}</a></li>
        <li className="btn"><a href={props.dispelLink} id={props.btnDispelId}>{props.dispelLabel}</a></li>
      </ul>
    </nav>
  );
}

export default Navigation;



